<template>
    <div>
        <div class="form-row clearfix">
            <div class="name"></div>
            <div class="form-limit">
                <input type="text" v-model="titleInfo.name" class="form-textbox" >
            </div>
        </div>
        <div class="form-row clearfix">
            <div class="name">
                <span class="add-btn" @click="addMoreClass()">+</span>
            </div>
            <div class="more-text" v-for="(item,i) in titleInfo.more" :key="i">
                <div class="ks-bt ks-link-bt fr" @click="selectLink($event,item)"></div>
                <div class="ks-bt ks-del-bt" @click="deleteMoreClass(i)"></div>
                <input type="text" class="form-textbox" style="width:110px;" v-model="item.text" placeholder="">
                <input type="text" class="form-textbox" style="width:210px; padding-right:45px;" placeholder="" v-model="item.url">
                
            </div>
            
        </div>
    </div>
</template>
<script>
import {domap,cloneData,deleteArray} from '@/assets/js/utils'
export default {
    name:'Title',
    props:[
        'titleInfo'
    ],
    methods:{

       /**
        * 
        */
        addMoreClass(){
            
            if(this.titleInfo.more.length > 2){
                this.$alert('3')
                return false
            }
            let more = cloneData(this.titleInfo.more)
            more.push({
                text:'',
                url:'#'
            })
            this.titleInfo.more = more
       },
       /**
        * 
        */
        deleteMoreClass(index){
            this.titleInfo.more = deleteArray(this.titleInfo.more,index)
        },
        selectLink(e,item){
            e.stopPropagation()
        }
    }
    
}
</script>
<style scoped>
.more-text{position: relative; padding-right: 30px; margin-top:10px;}
.more-text .ks-del-bt{ position: absolute;right: 0;}
.more-text .ks-link-bt{ position: absolute;right:38px;top:1px;height:32px; line-height:32px;border:0px;border-left:1px solid #e0e0e0;border-radius: 0px; }
.add-btn{width:24px;height:24px;border:1px solid #e0e0e0; text-align: center; line-height: 24px;border-radius: 4px; cursor: pointer; margin:5px 0 0 10px; display: inline-block}
.add-btn:hover{color:#e5412c;border:1px solid #e5412c}
</style>



