import axios from 'axios'

/**
 * 
 */
export function getStoreInfo(){
    return new Promise((resolve,reject)=>{
        axios.get(`/Json/index.php?act=Microportal.getWxdetail`).then(res=>{
            resolve(res.data.getWxdetail)
        })
        .catch(error=>{
            reject(error)
        })
    })
}

/**
 * 
 */
export function getStoreData(params){
    return new Promise((resolve,reject)=>{
        axios.post(`/Json/index.php?act=Microportal.import`,params).then(res=>{
            resolve(res.data.import)
        })
        .catch(error=>{
            reject(error)
        })
    })
}


/**
 * 
 */
export function getStoreViewConfig(params){
    return new Promise((resolve,reject)=>{
        axios.post(`/Json/index.php?act=Drag.wx_GetStoreViewConfig`,params).then(res=>{
            resolve(res.data.wx_GetStoreViewConfig)
        })
        .catch(error=>{
            reject(error)
        })
    })
}


