<template>
    <div>
        <set-link :setup="linkSetUp" />
        <div class="slide">
            <ul>
                <li v-for="(item,index) in list" :key="index">
                    <div class="img">
                        <img :src="item.img" />
                        <div class="select-img" @click="selectImg(item)"></div>
                    </div>
                    <div class="clearfix">
                        <span class="fl"></span>
                        <span class="ks-switch fl ml10" @click="item.flag = item.flag ? false : true" :class="item.flag && 'open'"></span>
                    </div>
                    <div v-show="item.flag" class="mt10">
                        <input class="form-textbox fl" style="width:175px;" type="text" v-model="item.url" />
                        <div class="ks-bt ks-link-bt fl ml10" @click.stop="linkSetUp.item = item;linkSetUp.show = true"></div>
                    </div>
                    <div class="ks-delete" @click="deleteInfo(index)"></div>
                </li>
            </ul>
        </div>
        <div align="center">
            <div class="ks-bt ks-bt-pur fn" @click="addInfoData()">banner</div>
        </div>
        
    </div>    
</template>
<script>
import SetLink from '@/pages/drag/management/SetLink'
import {domap,deleteArray,cloneArray} from '@/assets/js/utils'
export default {
    name:'Swiper',
    props:[
        'list'
    ],
    data(){
        return {
            linkSetUp:{
                item:{},
                show:false
            }
        }
    },
    methods:{
        /**
         * 
         */
        selectImg(){
            
        },
        /**
         * banner
         */
        deleteInfo(index){
            if(this.list.length <= 1){
                this.$alert('');
                return false
            }
            this.$parent.moduleManagement.list = deleteArray(this.list,index)
        },
        /**
         * banner
         */
        addInfoData(){
            if(this.list.length >= 10){
                this.$alert('10')
                return false
            };
            let arr = cloneArray(this.list)
            arr.push({
                flag:false,
                url:'',
                img:`${this.$config.siteUrl}Images/images/201806/5b189d39c63a7.png`
            })
            this.$parent.moduleManagement.list = arr
        },
        selectLink(){
           
        }
        
    },
    mounted(){
        let arr = cloneArray(this.list)
        domap(arr,item=>{
            if(item.flag === undefined){
                item.flag = item.url ? true : false
            }
        })
        this.$parent.moduleManagement.list = arr
    },
    components:{
        SetLink
    }
    
}
</script>
<style scoped>

/**/
.slide ul{padding-bottom: 20px}
.slide li{position: relative;min-height: 70px;padding:0 0 20px 135px;margin-top:20px;border-bottom: 1px solid #eee; }
.slide li .img{width:120px;height: 70px; position: absolute;left:0;top:0;border-radius: 4px; overflow: hidden;}
.slide li .img img{width: 100%; height: 100%;}
.slide .select-img{height: 22px; line-height: 22px; position: absolute;width: 100%;left:0;bottom:0; background: rgba(0,0,0,0.4); color:#fff; text-align: center; cursor: pointer;}
.ks-delete{
    top: 0;
    margin-top: -7px;
}

</style>



