import axios from 'axios'

/**
 * 
 */
export function saveStoreViewConfig(params){
    return new Promise((resolve,reject)=>{
        axios.post(`/Json/index.php?act=Drag.wx_SaveStoreViewConfig`,params).then(res=>{
            resolve(res.data.wx_SaveStoreViewConfig)
        })
        .catch(error=>{
            reject(error)
        })
    })
}

