<template>
    <div>
        <set-link :setup="linkSetUp" />
        <div class="nav">
            <ul>
                <li v-for="(item,index) in list" :key="index">
                    <div class="icon">
                        <img :src="item.icon">
                        <div class="select-img" @click="selectImg(item)"></div>
                    </div>
                    <div class="op"><span></span><input class="form-textbox" type="text" v-model="item.name" /></div>
                    <div class="op clearfix"><span></span>
                    <input class="form-textbox fl" type="text" v-model="item.url" style="width:140px;" />
                    <div class="ks-bt fl ks-link-bt ml5" @click.stop="linkSetUp.show = true"></div>
                    </div>
                    <div class="ks-delete" @click="deleteData(index)"></div>
                </li>
            </ul>
        </div>
        <div align="center"><div class="ks-bt ks-bt-pur fn" @click="addData()"></div></div>
    </div>    
</template>
<script>
import SetLink from '@/pages/drag/management/SetLink'
import {deleteArray,cloneArray} from '@/assets/js/utils'
export default {
    name:'Nav',
    props:[
        'list'
    ],
    data(){
        return {
            linkSetUp:{
                show:false
            }
        }
    },
    methods:{
        /**
         * 
         */
        selectImg(){

        },
        /**
         * 
         */
        deleteData(index){

            if(this.list.length <= 1){
                this.$alert('');
                return false
            }
            this.$parent.moduleManagement.list = deleteArray(this.list,index)

        },
        /**
         * 
         */
        addData(){
            if(this.list.length >= 5){
                this.$alert('5')
                return false
            };
            let arr = cloneArray(this.list)
            arr.push({
                name:'',
                icon:`${this.$config.siteUrl}Public/wechat/images/microportal/nopic.png`,
                url:"#"
            })
            this.$parent.moduleManagement.list = arr

        }
    },
    components:{
        SetLink
    }
    
}
</script>
<style scoped>
/**/
.nav{padding-bottom: 15px}
.nav li{padding: 10px 10px 10px 76px; position: relative; min-height: 66px;}
.nav .icon{width:64px;height: 64px; position: absolute;left:0;top:15px; border:1px solid #eee;border-radius: 4px; overflow: hidden;}
.nav .icon img{height: 48px; display: block;margin:0 auto;margin-top:8px;}
.nav li{border-bottom: 1px solid #eee}
.nav .op{position: relative; line-height: 36px;padding:0 25px 0 70px;margin-top:5px}
.nav .op span{position: absolute;left:0;top:0; color:#999}
.nav .select-img{height: 20px; line-height: 20px; position: absolute;width: 100%;left:0;bottom:0; background: rgba(0,0,0,0.4); color:#fff; text-align: center; cursor: pointer;}

</style>



