<template>
    <div>
        <div class="form-row clearfix">
            <span class="name"></span>
            <div class="clearfix">
                <label class="fl" :class="contSource == 0 && 'label-on'">
                <input type="radio" value="0" v-model="contSource"></label>
                <label class="fl" :class="contSource == 1 && 'label-on'">
                <input type="radio"  value="1" v-model="contSource">
                </label>
            </div>
        </div>
        <select-item  v-if="contSource == 0" :config="config" />
        <div v-else>
            <div class="form-row clearfix">
                <span class="name"></span>
                <select class="form-select" v-model="config.data.courseType">
                    <option value="-1001"></option>
                    <option value="4"></option>
                    <option value="3"></option>
                    <option value="1"></option>
                    <option value="2"></option>
                </select>
            </div>
            <div class="form-row clearfix">
                <span class="name"></span>
                <select class="form-select" v-model="config.data.chargetype">
                    <option value="-1001"></option>
                    <option value="1"></option>
                    <option value="2"></option>
                    <option value="0"></option>
                </select>
            </div>
            <div class="form-row clearfix">
                <span class="name"></span>
                <input v-model="config.maxperpage" type="text" @keyup="limitNumber(config.maxperpage,20)" class="form-textbox fl" style="width:150px"><span class="form-tips fsize12 ml10" style="line-height:34px">20</span>
            </div>
        </div>
        
    </div>
</template>
<script>
import SelectItem from '@/pages/drag/management/SelectItem'
import {getStoreData} from '@/api/store'
import {cloneData,limitNumber,domap} from '@/assets/js/utils'

export default {
    name:'CourseManagement',
    props:[
        'nodeName'
    ],
    data(){
        return {
            config:{
                data:{}
            },
            contSource:null,
            watchFlag:false
        }
    },
    watch:{
        config:{
            handler(){
                if(this.watchFlag){
                    /**
                     * 
                     */
                    getStoreData([
                        this.config   
                    ]).then(res=>{
                        let list = res[this.config.callKeyName].list;
                        this.$parent[this.nodeName].list = list
                    })
                }
            },
            deep:true
        },
        contSource(){
            if(this.watchFlag){
                
                if(this.contSource == 0){
                    /**
                     * 
                     */
                    this.config.data.courseType = -1001;
                    this.config.data.chargetype = -1001;
                }else{
 
                    /**
                     * 
                     */
                    this.config.data.sql_or_like = [];
                    
                }
                
            }
        }
    },
    methods:{
        limitNumber(v,num){
            let nv = limitNumber(v,num);
            if(!nv){
                return false
            }else{
                this.config.maxperpage = nv
                
            }
        }
    },
    mounted(){
        /**
         * 
         */
        let $parentConfig = this.$parent[this.nodeName].api;
        let config = cloneData($parentConfig);
        config.data = config.data || {};
        config.data.courseType = config.data.courseType || -1001;
        config.data.chargetype = config.data.chargetype || -1001;
        config.data.maxperpage = config.data.maxperpage || 10;
        config.data.sql_or_like = config.data.sql_or_like || [];
        $parentConfig = config;
        
        if(config.data.sql_or_like.courseid){
            this.contSource = 0
        }else{
            this.contSource = 1
        }
        this.config = $parentConfig
        this.$nextTick(()=>{
            this.watchFlag = true
        })

    },
    components:{
        SelectItem
    }
    
}
</script>
<style scoped>


</style>



